	module.exports = {"data":{"markdownRemark":{"html":"<h3>Communities</h3>\n<p><a href=\"https://www.codewars.com/users/nperon\">codewars</a></p>\n<p><a href=\"https://stackoverflow.com/users/10363360\">stackoverflow</a></p>\n<p><a href=\"https://github.com/nperon\">github</a></p>\n<p><a href=\"https://www.linkedin.com/in/nicolas-peron-52b250140/\">linkedin</a></p>\n<p><a href=\"https://www.facebook.com/profile.php?id=100012153432065\">facebook</a></p>\n<p><a href=\"https://twitter.com/NicolasPeron5\">twitter</a></p>\n<h3>Trainings / Completed online courses</h3>\n<p><a href=\"http://ude.my/UC-LTEODNLK\"> via @udemy: Ionic 4 Crash Course</a></p>\n<p><a href=\"https://www.udemy.com/certificate/UC-3BOGQ0KF/\"> via @udemy: Redux Saga (with React and Redux): Fast-track intro course</a></p>\n<p><a href=\"https://www.udemy.com/certificate/UC-WVGZWOCD/\"> via @udemy: Up and Runninng with PostgreSQL 11</a></p>\n<p><a href=\"https://www.udemy.com/certificate/UC-4TKNLZQ4/\"> via @udemy: The Ultimate MySQL Bootcamp: Go from SQL Beginner to Expert</a></p>\n<p><a href=\"https://www.udemy.com/certificate/UC-ITXKXIZ7/\"> via @udemy: Asynchronous JavaScript: Promises, Callbacks, Async Await</a></p>\n<p><a href=\"https://www.udemy.com/certificate/UC-81NFLFR5/\"> via @udemy: Jenkins for Professionals</a></p>\n<p><a href=\"https://www.udemy.com/certificate/UC-52TXS07R/\"> via @udemy: JavaScript Cookie Tester web application</a></p>\n<p><a href=\"https://www.udemy.com/certificate/UC-D1K1BCTL/\"> via @udemy: Object Oriented PHP &#x26; MVC</a></p>\n<p><a href=\"https://www.udemy.com/certificate/UC-b54a41ff-a687-4fcb-9a0c-e60e1f4ac1f3/\"> via @udemy: Hands-On Spring Security 5.x</a></p>","frontmatter":{"title":"Nicolas's CV","date":"January 01, 2019","path":"/cv","tags":["cv","work"],"excerpt":"My CV"}}},"pathContext":{"prev":{"html":"<h3>Create a container with a mongo database and connect to it</h3>\n<p>Start with displaying all currently running containers:</p>\n<pre><code class=\"language-bash\">docker ps\n</code></pre>\n<p>Then create locally the container associated with the mongo docker image:</p>\n<pre><code class=\"language-bash\">docker run -p 27017:27017 -d mongo\n</code></pre>\n<p>Executing another time <code>docker ps</code> results in information on the created container like the following being displayed:</p>\n<pre><code>CONTAINER ID        IMAGE               COMMAND                  CREATED             STATUS              PORTS                      NAMES\n2c6a61aba41b        mongo               \"docker-entrypoint.s…\"   3 minutes ago       Up 3 minutes        0.0.0.0:27017->27017/tcp   clever_dubinsky\n</code></pre>\n<p>Connection to the database at url localhost:27017 can be opened using for instance the robo-3t nosql database editor.\nFinally, the container when not needed anymore can be stopped with:</p>\n<pre><code>docker stop 2c6a61aba41b\n</code></pre>","id":"/Users/nperon/Documents/workshop/src/pages/2020-03-31-docker-recipes/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2019-03-05T22:35:12.235Z","path":"/docker","title":"Docker Recipes","excerpt":"Some Docker Recipes","tags":["docker","container","devops"]}},"next":null}}

